import React from 'react';
import ListGroup from 'react-bootstrap/ListGroup';
import {approx} from '../utils';

const OrientationDisplay = ({eventOr, eventMo}) => {
  
  const setX = () => {try {return approx(eventOr.alpha)} catch {return 0;}}
  const setY = () => {try {return approx(eventOr.beta)} catch {return 0;}}
  const setZ = () => {try {return approx(eventOr.gamma)} catch {return 0;}}
  const setXAcc = () => {try {return approx(eventMo.acceleration.x)} catch {return 0;}}
  const setYAcc = () => {try {return approx(eventMo.acceleration.y)} catch {return 0;}}
  const setZAcc = () => {try {return approx(eventMo.acceleration.z)} catch {return 0;}}
  const setAccInt = () => {try {return approx(eventMo.interval)} catch {return 0;}}

  return (
    <ListGroup>
      <ListGroup.Item>alpha<span className="float-right">{setX()}</span></ListGroup.Item>
      <ListGroup.Item>beta<span className="float-right">{setY()}</span></ListGroup.Item>
      <ListGroup.Item>gamma<span className="float-right">{setZ()}</span></ListGroup.Item>
      <ListGroup.Item>Acc X<span className="float-right">{setXAcc()}</span></ListGroup.Item>
      <ListGroup.Item>Acc Y<span className="float-right">{setYAcc()}</span></ListGroup.Item>
      <ListGroup.Item>Acc Z<span className="float-right">{setZAcc()}</span></ListGroup.Item>
      <ListGroup.Item>Acc Interval<span className="float-right">{setAccInt()}</span></ListGroup.Item>
  </ListGroup>
  );
}
export default OrientationDisplay;